import { GLOB_DATA } from "../GLOB_DATA.js";

//==========================

class mLocalStorage {
    loadLocalStorageBis() {
        // console.log('=======================');
        // console.log('Запуск loadLocalStorageBis :');
        let data = localStorage.getItem('myLocalStorageBis');
        // console.log('data=');
        // console.log(data);
        if (data) {
            GLOB_DATA.locStorData = JSON.parse(data);
        };
        // console.log('GLOB_DATA.locStorData=');
        // console.log(GLOB_DATA.locStorData);

        // console.log('ПРОЧТЕНИЕ LocalStorage, GLOB_DATA.locStorData = ');
        // console.log(GLOB_DATA.locStorData);
        // return GLOB_DATA.locStorData;
    }
    // --------------------------
    saveLocalStorageBis() {
        // console.log('СОХРАНЕНИЕ GLOB_DATA.locStorData= ');
        // console.log(GLOB_DATA.locStorData);
        localStorage.setItem('myLocalStorageBis', JSON.stringify(GLOB_DATA.locStorData));
    }
    // --------------------------
    deleteLocaleStorageBis() {
        localStorage.removeItem('myLocalStorageBis');
    }
}

//==========================

export default new mLocalStorage;
