import React from "react";
import { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { MainMenuButtonDropDown, MainMenuButtonDropDown_short } from "../navMainMenu/MainMenuButtonDropDown.jsx";
import mLocalStorage from "../bisFormsAndFunctions/localStorageBis.js";
import { GLOB_DATA } from "../GLOB_DATA.js";
import BisGlobFunctions from "../bisFormsAndFunctions/bisGlobFunctions";
import m_SVG_ICONS from '../m_SVG_ICONS.js';
import { loginService } from "../bisFormsAndFunctions/loginService.js";
import Log from 'jwt-encode';
import jwt_decode from 'jwt-decode';
import mConfig from '../config.js';
import { UserAvatarBlock } from "../bisFormsAndFunctions/bis_GlobContenner.jsx";
import DownDropLanguageChange from "./DownDropLanguageChange.jsx";

function Header() {
    const [maxWidthUserNameField, set_maxWidthUserNameField] = useState("200px");
    const my_dispatch = useDispatch();
    const currentUser_Redux = useSelector(mState => mState.m_Redus_currentUser.currentUser);
    const accessToken_Redux = useSelector(mState => mState.m_Redus_accessToken.accessToken);
    const activeBisProject_INDEX_FromRedux = useSelector(mState => mState.m_Redus_ActiveProjectAndSubproject_Bis.currentActiveProject_INDEX);
    const bisDataRedux = useSelector(mState => mState.m_Redus_bisData.bisDataRedux);
    const selected_private_or_bis_FromRedux = useSelector(mState => mState.m_Redus_select_Priv_or_Bis.select_priv_bis);
    const colorShemeNumber_Redux = useSelector(mState => mState.m_Redus_colorSheme.current_colorSheme);
    const soundOnStatus_Redux = useSelector(mState => mState.m_Redus_soundOnStatus.current_soundOnStatus);
    const languageChoosed_Redux = useSelector(mState => mState.m_Redus_languageChoosed.current_languageChoosed);
    const shortMenuType_Redux = useSelector(mState => mState.reduser_shortMenuType.current_shortMenuType);


    const mediaAdaptationData_mobilAdaptationName_FromRedux = useSelector(mState => mState.m_Redus_mediaAdaptionData.mobilAdaptationName);

    useEffect(() => { // при изменении цветовой схемы
        // устанавливаем стили для body через useEffect
        document.body.className = ("colorStyle_cheam_" + colorShemeNumber_Redux);
        // возможные варианты изменения стилей:
        // div.classList.remove("foo");
        // div.classList.add("anotherclass");

        // сохраняем в localStorage только в случае, если установка осуществилась вручную, а не из localStorage, чтобы измежыть рекурсии
        /* 
                if (GLOB_DATA.firstLoadStorage === true) {
                    if (colorShemeNumber_Redux) {
                        GLOB_DATA.locStorData.colorSheme = colorShemeNumber_Redux;
                        mLocalStorage.saveLocalStorageBis();
                    }
                }
                 */
    }, [colorShemeNumber_Redux] //
    ); // при изменении цветовой схемы

    return (
        <header
            style={{
                position: "fixed",
                top: 0,
                right: 0,
                left: 0,
                margin: 0,
                padding: 0,
                border: "none",
                zIndex: 5,

                borderBottom: "solid 1px",
            }}
        >

            <div className="headerContenner"
                style={{
                    display: "flex",
                    flexDirection: "row",
                    flexWrap: "nowrap",
                    alignItems: "center",
                    justifyContent: "space-between",
                    height: "50px",
                    minHeight: "50px",
                    maxHeight: "50px",
                }}
            >

                <div className="h_cont_left"
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "left",
                        alignItems: "center",
                        height: "100%",
                    }}
                >

                    {/*     Это блок компактного меню         */}
                    <div className="blockFor_DropDownShort">
                        <MainMenuButtonDropDown_short />
                    </div>

                    <div className="logoBlock"
                        style={{
                            // position: "relative",
                            display: "flex",
                            flexDirection: "row",
                            height: "36px",
                            marginLeft: "4px",
                            marginRight: "10px",
                        }}
                    >

                        {
                            // Лептоповая версия + светлая тема
                            (!mediaAdaptationData_mobilAdaptationName_FromRedux &&
                                (colorShemeNumber_Redux == 1))
                                ?
                                < img src={mConfig.noReactStaticFilesAdress + "/img/logoMain__WhiteTema.png"}
                                    height="100%"
                                    title="Lite Projects Manager"
                                />
                                :
                                null
                        }

                        {
                            // Лептоповая версия + темная тема
                            (!mediaAdaptationData_mobilAdaptationName_FromRedux &&
                                (colorShemeNumber_Redux == 2))
                                ?
                                < img src={mConfig.noReactStaticFilesAdress + "/img/logoMain__BlackTema.png"}
                                    height="100%"
                                    title="Lite Projects Manager"
                                />
                                :
                                null
                        }

                        {
                            // Мобильная версия + светлая тема
                            (mediaAdaptationData_mobilAdaptationName_FromRedux &&
                                (colorShemeNumber_Redux == 1))
                                ?
                                < img src={mConfig.noReactStaticFilesAdress + "/img/logoMainMobile__WhiteTema.png"}
                                    height="100%"
                                    title="Lite Projects Manager"
                                />
                                :
                                null
                        }

                        {
                            // Мобильная версия + темная тема
                            (mediaAdaptationData_mobilAdaptationName_FromRedux &&
                                (colorShemeNumber_Redux == 2))
                                ?
                                < img src={mConfig.noReactStaticFilesAdress + "/img/logoMainMobile__BlackTema.png"}
                                    height="100%"
                                    title="Lite Projects Manager"
                                />
                                :
                                null
                        }

                    </div>

                    {/*     Это блок для кнопки главного меню         */}
                    <div className="contennerFor_mainMenuButton">
                        <MainMenuButtonDropDown />
                    </div>


                </div>

                <div className="h_cont_center"
                    style={{
                        display: shortMenuType_Redux ? "none" : "flex",
                        flexDirection: "row",
                        justifyContent: "space-between",
                        alignItems: "center",
                        height: "100%",
                        width: "100%",


                    }}

                >

                    <div className="h_center_left"
                        style={{
                            display: "flex",
                            flexDirection: "row",
                            alignItems: "center",
                        }}
                    >

                        <div className="twoButtonsContenner"
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                // paddingLeft: "10px",
                                alignItems: "center"
                            }}
                        >
                            <div className="roundBbutton"
                                style={{
                                    //  height: "26px",
                                }}
                                title={
                                    (soundOnStatus_Redux == true)
                                        ? "Sound is On"
                                        : "Sound is Off"
                                }

                                onClickCapture={() => {
                                    BisGlobFunctions.changeSoundOn();
                                }}
                            >
                                <div className="svg__btnSymbol" >
                                    {
                                        (soundOnStatus_Redux == true)
                                            ? <m_SVG_ICONS.signalOn className="mSvgFile" />
                                            : <m_SVG_ICONS.signalOff className="mSvgFile" />
                                    }
                                </div>
                            </div>

                            <div className="roundBbutton colorsBtn"
                                title="Color theam"
                                style={{
                                    fontSize: "1.2em",
                                    fontWeight: "bold",
                                    borderWidth: "2px",
                                }}
                                onClick={() => {
                                    BisGlobFunctions.changeColorSheme();
                                }}
                            >
                                {colorShemeNumber_Redux}
                            </div>

                        </div>
                    </div>


                    <div className="h_center_center"
                        style={{
                            display: "flex",
                            flexDirection: "row",
                            alignItems: "center",
                        }}
                    >

                        <div className="Contacts_list"
                            title="Contacts_list"
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                alignItems: "center",
                                marginLeft: "6px",
                                marginRight: "6px",
                                height: "24px",

                                cursor: 'pointer',
                            }}
                            onClickCapture={() => {
                                // Альтернатива disabled - при необходимости прерываем onClick:
                                if (!(accessToken_Redux && currentUser_Redux)) return null;
                                my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_setContactList" });
                            }}
                        >

                            <div className="svg__btnSymbol" >
                                <m_SVG_ICONS.contact_list className="mSvgFile" />
                            </div>
                        </div>

                        <div className="AboutSoft"
                            title="About service"
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                alignItems: "center",
                                marginLeft: "6px",
                                marginRight: "6px",
                                height: "36px",

                                cursor: 'pointer',
                            }}
                            onClickCapture={() => {
                                my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_aboutSoft" });
                            }}
                        >

                            <div className="roundBbutton svg__btnSymbol" >
                                <m_SVG_ICONS.aboutServise className="mSvgFile" />
                            </div>
                        </div>

                    </div>


                    <div className="h_center_right"
                        style={{
                            display: "flex",
                            flexDirection: "row",
                            alignItems: "center",
                        }}
                    >
                        <div className="languageBlock"
                            style={{
                                display: "flex",
                                flexDirection: "row",
                                alignItems: "center",
                                marginLeft: "6px",
                            }}
                            title="Select language"
                        >
                            <DownDropLanguageChange />
                        </div>
                    </div>



                </div>

                <div className="h_cont_right"
                    style={{
                        // justifyContent: "flex-end",
                        // maxWidth: "calc(100% - 230px)",
                        // marginLeft: "20px",
                        marginRight: "4px",
                        // paddingRight: "4px",
                        //  height: "100%",
                        // background: "red",
                    }}
                >

                    {
                        accessToken_Redux
                            ?
                            <div className="userHeadBlock"
                                style={{
                                    height: "100%",
                                }}
                            >
                                <ButtonMenuSlide_userSettings />
                            </div>
                            :
                            <div className="blockLogIn">
                                <button className="mBtn_01"
                                    style={{ cursor: "pointer" }}
                                    onClickCapture={() => {
                                        my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                        my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "subDialog_currentDialogFormIs_SubDialog_logIn_Form" });
                                    }}
                                >
                                    Log In
                                </button>
                            </div>
                    }

                </div>

            </div>

        </header >
    )
}

// ---------------------------------------

function ButtonMenuSlide_userSettings() {
    const [isOpenMenu, set_isOpenMenu] = useState(false);
    const my_dispatch = useDispatch();
    // const bisDataRedux = useSelector(mState => mState.m_Redus_bisData.bisDataRedux);
    // const chatListRedux = useSelector(mState => mState.m_Redus_chatListRedux.chatListRedux);
    const currentUser_Redux = useSelector(mState => mState.m_Redus_currentUser.currentUser);
    const accessToken_Redux = useSelector(mState => mState.m_Redus_accessToken.accessToken);
    const languageChoosed_Redux = useSelector(mState => mState.m_Redus_languageChoosed.current_languageChoosed);

    return (
        // Это непосредственно кнопка
        <div className="ButtonSlideMenu_userSettings "
            style={{
                position: "relative",
                // display: "block",
                // доп параметры конкретно для этой кнопки
                //  height: "16px", zIndex: "6",
                outline: "none", // необх, чтобы в мобильном приложении не было вокруг рамки при активации
                height: "100%",
                // height: "50px",
                marginLeft: "10px",
                cursor: "pointer",
            }}
            // важно - присваиваем блоку произвольное значение свойства tabIndex, чтобы он начал поддерживать фокусировку, поскольку по умолчанию элементы типа DIV, NAV и т.п. не поддерживают получение фокуса
            tabIndex={1002}
            onClickCapture={() => {
                // console.log("CLICK");
                // при повторных кликах открываем/закрываем меню
                set_isOpenMenu(!isOpenMenu);
            }}
            onBlur={() => set_isOpenMenu(false)}
            onMouseLeave={() => set_isOpenMenu(false)}
        >
            <UserAvatarBlock
                userName={currentUser_Redux}
                user_Email={currentUser_Redux}
                // isOnlineCurrentUser={BisGlobFunctions.getOnlineStatusCurrentUser(currentUser_Redux)}
                isOnlineCurrentUser={false}  // отключаем онлайн статус на главной иконке пользователя
                avatarSize_PX="36px"
            />

            {/* Это фон выпадающего меню - накладываются в том числе на саму кнопку   */}
            <div className={isOpenMenu ? "ContennerSlideMenu " : "mDisplayNone"}
                style={{
                    position: "absolute",
                    right: 0,
                    top: "20px",
                    // padding: "10px",
                    paddingTop: "20px",
                    zIndex: 2,

                    // border: 'solid 1px grey'
                }}
            >

                {/* Это контент выпадающего меню      */}
                <div className=" colorClass__dropDown_menu"
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        // background: "gray",
                        // marginTop: "40px",
                        padding: "10px",
                        width: "250px",
                    }}
                >
                    <br />
                    <div className="menuSubContent_Button"
                        onClick={async () => {
                            // Альтернатива disabled - при необходимости прерываем onClick:
                            if (!(accessToken_Redux && currentUser_Redux)) return null;

                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_userSettingsForm" });
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Benutzereinstellungen" : null}
                        {(languageChoosed_Redux == "pl") ? "Ustawienia użytkownika" : null}
                        {(languageChoosed_Redux == "ua") ? "Налаштування користувача" : null}
                        {(languageChoosed_Redux == "ru") ? "Настройки пользователя" : null}
                        {(languageChoosed_Redux == "en") ? "User Settings" : null}
                    </div>
                    <br />
                    <div className="menuSubContent_Button"
                        onClickCapture={async () => {
                            // Альтернатива disabled - при необходимости прерываем onClick:
                            if (!(accessToken_Redux && currentUser_Redux)) return null;

                            // Сразу обнуляем на клиенте
                            BisGlobFunctions.setLogOutData();
                            // отправляем запрос серверу для удаления токена
                            let myPromise = new Promise((resolve, reject) => {
                                let postDataToServer = {
                                    eMail: currentUser_Redux,
                                    mGadgetProcess_ID: GLOB_DATA.mGadgetProcess_ID,
                                };

                                try {
                                    let mURL = mConfig.serverPort + "/logOutOneGadget";
                                    fetch(mURL, {
                                        method: 'post',
                                        headers: {
                                            accesstoken: GLOB_DATA.accessToken_readOnly,
                                            'Content-Type': 'application/json',
                                        },
                                        body: JSON.stringify({
                                            postDataToServer
                                        })
                                    })
                                        .then(res => res.json())
                                        .then((res) => {
                                            console.log("Ответ сервера logOutOneGadget:");
                                            console.log(res);
                                            resolve(res);
                                        })


                                } catch (error) {
                                    console.log(error);
                                    reject(error);
                                }
                            })

                            // BisGlobFunctions.setCarrentUser(null);
                            // BisGlobFunctions.setAccessToken(null);
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Hinausgehen" : null}
                        {(languageChoosed_Redux == "pl") ? "Wychodzić" : null}
                        {(languageChoosed_Redux == "ua") ? "Вийти" : null}
                        {(languageChoosed_Redux == "ru") ? "Выйти" : null}
                        {(languageChoosed_Redux == "en") ? "Log out" : null}
                    </div>
                    <br />
                    <div className="menuSubContent_Button"
                        onClickCapture={async () => {
                            // Альтернатива disabled - при необходимости прерываем onClick:
                            if (!(accessToken_Redux && currentUser_Redux)) return null;

                            let myPromise = new Promise((resolve, reject) => {
                                let postDataToServer = {
                                    eMail: currentUser_Redux,
                                };

                                try {
                                    let mURL = mConfig.serverPort + "/logOutAllGadgets";
                                    fetch(mURL, {
                                        method: 'post',
                                        headers: {
                                            accesstoken: GLOB_DATA.accessToken_readOnly,
                                            'Content-Type': 'application/json',
                                        },
                                        body: JSON.stringify({
                                            postDataToServer: postDataToServer,
                                        })
                                    })
                                        .then(res => res.json())
                                        .then((res) => {
                                            console.log("Ответ сервера logOutAllGadgets:");
                                            console.log(res);
                                            resolve(res);
                                        })

                                } catch (error) {
                                    reject(error);
                                }
                            })
                            // return await myPromise; // Ничего не предпринимаем После получения ответа из Coockie должен удаляться refresh-токен
                            // обнуляем accessToken_Redux
                            BisGlobFunctions.setAccessToken(null);
                            // ВАЖНО - после обнуления accessToken_Redux, потом  обнуляем текущего юзера
                            BisGlobFunctions.setCarrentUser(null);
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Melden Sie sich von allen Gadgets ab" : null}
                        {(languageChoosed_Redux == "pl") ? "Wyloguj się ze wszystkich gadżetów" : null}
                        {(languageChoosed_Redux == "ua") ? "Вийти зі всіх гаджетів" : null}
                        {(languageChoosed_Redux == "ru") ? "Выйти из всех гаджетов " : null}
                        {(languageChoosed_Redux == "en") ? "Log out of all gadgets" : null}
                    </div>
                    <br />
                    <div className="menuSubContent_Button"
                        onClickCapture={() => {
                            // Альтернатива disabled - при необходимости прерываем onClick:
                            if (!(accessToken_Redux && currentUser_Redux)) return null;

                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_changePasswForm" });
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Kennwort ändern" : null}
                        {(languageChoosed_Redux == "pl") ? "Zmień hasło" : null}
                        {(languageChoosed_Redux == "ua") ? "змінити пароль" : null}
                        {(languageChoosed_Redux == "ru") ? "Изменить пароль" : null}
                        {(languageChoosed_Redux == "en") ? "Change password" : null}
                    </div>
                    <br />
                </div>

            </div>

        </div>


    )
}

export default Header;




