async function confirmOnlineStatus(user_Email, current_RequestID) {
    // console.log("ЗАПУСК confirmOnlineStatus: " + current_RequestID);
    // console.log("accessToken_readOnly= " + GLOB_DATA.accessToken_readOnly);

    if (!GLOB_DATA.accessToken_readOnly) {
        // console.log("=================  Прерываем confirmOnlineStatus - ОТСУТСТВУЕТ  accessToken");
        return;
    }

    if (GLOB_DATA.repeatingReq_ID___mainProcess != current_RequestID) {
        // console.log("=================  Прерываем предыдущий рецикл confirmOnlineStatus - БОЛЕЕ НЕ АКТУАЛЕН " + current_RequestID);
        return;
    }

    let postDataToServer = {
        user_Email: user_Email,
        user_KnownIndexIbServerReestr: null
    }
    let mURL = mConfig.serverPort + "/confirmOnlineStatus";

    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer
            })
        })
            // Тут проверяем Статус ответа сервера
            .then(res => {
                // выполняем проверку наличие ответа 401
                BisGlobFunctions.control_401_res(res, postDataToServer);
                return res;
            })
            .then(res => res.json())
            .then((res) => {
                // console.log("Возвращен ответ psevdoServer из confirmOnlineStatus: ");
                // console.log(res);
                return res;
            })
            .then(() => {

                // проверяем, не именился ли текущий пользователь 
                if (GLOB_DATA.currentUser_readOnly == user_Email) {
                    setTimeout(() => {
                        // console.log("Повторный плановый запуск confirmOnlineStatus - setTimeout 20000");
                        confirmOnlineStatus(user_Email, current_RequestID);
                    }, 60000) // перезапуск 
                }
            }
            )

    } catch (error) {
        console.log("Ошибка из confirmOnlineStatus");
        console.log(error);
        setTimeout(() => {
            console.log("Внеплановый запуск confirmOnlineStatus из error 5000ms");
            confirmOnlineStatus(user_Email);
        }, 5000)  // перезапуск
    }
}

