// эта функция запрашивает статус пользователей из контакт-листа текущего Админа
async function getUsersOnlineStatusFromServer_forContactListCurrentAdmin(user_Email) {
    // console.log("ЗАПУСК getUsersOnlineStatusFromServer_forContactListCurrentAdmin: ");

    if (!GLOB_DATA.accessToken_readOnly) return;

    let postDataToServer = {
        user_Email: user_Email,
        user_KnownIndexIbServerReestr: null
    }
    let mURL = mConfig.serverPort + "/getUsersOnlineStatusFromServer_forContactListCurrentAdmin";

    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer
            })
        })
            .then(res => res.json())
            .then((res) => {
                console.log("Возвращен ответ psevdoServer из getUsersOnlineStatusFromServer_forContactListCurrentAdmin: ");
                console.log(res);
                GLOB_DATA.update_usersOnlineStatusRedux(res);
            })
            .then(() => {
                setTimeout(() => {
                    console.log("Повторный плановый запуск getUsersOnlineStatusFromServer_forContactListCurrentAdmin - setTimeout 60000");
                    getUsersOnlineStatusFromServer_forContactListCurrentAdmin(user_Email);
                }, 60000) // перезапуск
            }
            )

    } catch (error) {
        console.log("Ошибка из getUsersOnlineStatusFromServer_forContactListCurrentAdmin");
        console.log(error);
        setTimeout(() => {
            console.log("Внеплановый запуск getUsersOnlineStatusFromServer_forContactListCurrentAdmin из error 5000ms");
            getUsersOnlineStatusFromServer_forContactListCurrentAdmin(user_Email);
        }, 5000)  // перезапуск
    }
}

