// эта функция запрашивает статус пользователей активного проекта
// функция перезапускается циклически, но должна прерываться при изменении активного проекта. Ддя контроля необходимости перезапуска используется аргумент "inputUnicodOfRequest_getUsersOnlineStatus", который каждый раз перед перезапуском функции сравнивается с актуальным уникодом в глобальной переменной.
async function getUsersOnlineStatusFromServer_forCurrentProject(
    project_ID,
    knownProjectIndex_inServer,
    inputUnicodOfRequest_getUsersOnlineStatus) {

    // console.log("ЗАПУСК getUsersOnlineStatusFromServer_forCurrentProject, accessToken= ");
    // console.log(GLOB_DATA.accessToken_readOnly);
    // console.log("project_ID= " + project_ID);
    // console.log("activeBisProject_INDEX_FromRedux_readOnly= " + GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly);



    if (!GLOB_DATA.accessToken_readOnly) {
        // console.log("=================  Прерываем getUsersOnlineStatusFromServer_forCurrentProject - ОТСУТСТВУЕТ  accessToken");
        return;
    }

    if (!project_ID) return;
    // если текущий проект был удален этим пользователем
    if (!(GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly != null && GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly >= 0)) {
        // console.log("=================  Прерываем getUsersOnlineStatusFromServer_forCurrentProject");
        return;
    }

    let postDataToServer = {
        project_ID: project_ID,
        knownProjectIndex_inServer: knownProjectIndex_inServer
    }
    let mURL = mConfig.serverPort + "/getUsersOnlineStatusFromServer_forCurrentProject";

    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer
            })
        })
            .then(res => res.json())
            .then((res) => {
                // console.log("Возвращен ответ psevdoServer из getUsersOnlineStatusFromServer_forContactListCurrentAdmin: ");
                // console.log(res);
                // проверяем, актуален ли еще текущий запрос
                if (GLOB_DATA.unicodOfRequest_getUsersOnlineStatus == inputUnicodOfRequest_getUsersOnlineStatus) {

                    GLOB_DATA.update_usersOnlineStatusRedux(res);

                    setTimeout(() => {
                        // console.log("Повторный плановый запуск getUsersOnlineStatusFromServer_forContactListCurrentAdmin - setTimeout 60000");
                        getUsersOnlineStatusFromServer_forCurrentProject(project_ID, knownProjectIndex_inServer, inputUnicodOfRequest_getUsersOnlineStatus);
                    }, 60000) // перезапуск
                }
                else {
                    // console.log("ЗАПРОС БОЛЕЕ НЕ АКТУАЛЕН  - НЕ ПЕРЕЗАПУСКАЕМ");
                }
            })

    } catch (error) {
        console.log("Ошибка из getUsersOnlineStatusFromServer_forCurrentProject");
        console.log(error);
        setTimeout(() => {
            console.log("Внеплановый запуск getUsersOnlineStatusFromServer_forCurrentProject из error 5000ms");
            getUsersOnlineStatusFromServer_forCurrentProject(project_ID, knownProjectIndex_inServer, inputUnicodOfRequest_getUsersOnlineStatus);
        }, 5000)  // перезапуск
    }
}

