
import BisGlobFunctions from '../bisFormsAndFunctions/bisGlobFunctions.js';
import { GLOB_DATA } from "../GLOB_DATA.js";
import mLocalStorage from "../bisFormsAndFunctions/localStorageBis.js";

// --------------------------------------
const default_priv_OR_bis_select = {
    select_priv_bis: "bis",
}

export const reduser_select_Priv_or_Bis = (my_state = default_priv_OR_bis_select, my_action) => {
    switch (my_action.type) {

        case "SET_select_priv_bis": {
            console.clear();
            console.log("Из диспатч - select_priv_bis= " + my_action.select_Priv_or_Bis);
            return { my_state, select_priv_bis: my_action.select_Priv_or_Bis };
            break;
        }

        default: return my_state;
    }
}

// --------------------------------------
const default_colorSheme = {
    current_colorSheme: 2,
}

export const reduser_colorSheme = (my_state = default_colorSheme, my_action) => {
    switch (my_action.type) {
        case "set_colorShemeCurrentNumber": {
            //  console.log("СРАБОТАЛ РЕДЮСЕР -set_colorShemeCurrentNumber-, new_colorSheme= " + my_action.new_colorShemeNumber);
            GLOB_DATA.currentColorSheme_readOnly = my_action.new_colorShemeNumber;
            return { ...my_state, current_colorSheme: my_action.new_colorShemeNumber };
            break;
        }

        default: return my_state;
    }
}

