const default_languageChoosed = {
    current_languageChoosed: BisGlobFunctions.getDefault_languageFromBrowser(),
}

export const reduser_languageChoosed = (my_state = default_languageChoosed, my_action) => {
    switch (my_action.type) {
        case "set_languageChoosed": {
            GLOB_DATA.languageChoosed_VECTOR = my_action.new_languageChoosed;
            return { ...my_state, current_languageChoosed: my_action.new_languageChoosed };
            break;
        }

        default: return my_state;
    }
}

// --------------------------------------

const default_shortMenuType = {
    current_shortMenuType: GLOB_DATA.shortMenuType,
}

export const reduser_shortMenuType = (mState = default_shortMenuType, my_action) => {
    switch (my_action.type) {
        case "set_shortMenuType": {
            GLOB_DATA.shortMenuType = my_action.newValue;
            GLOB_DATA.locStorData.shortMenuType = my_action.newValue;
            mLocalStorage.saveLocalStorageBis();
            return { mState, current_shortMenuType: my_action.newValue };
            break;
        }

        default: return mState;
    }
}

// --------------------------------------

const default_DialogForm = {
    isDialogVisible: false,
    carrentActiveSubdialog: null,
    dialogFormRequestData: null,
    // Это для диалоговых форм  2 уровня - для Alert Confirm Promt
    carrentActiveSubdialog_2level: null,
    carrentActiveSubdialog_3level: null, // резерв
    carrentActiveSubdialog_4level: null, // резерв
    // Это для форм  5 уровня - для Loadera
    carrentActiveSubdialog_5level: null,

}

export const reduser_DialogForm = (
    my_state = default_DialogForm,
    my_action) => {

    switch (my_action.type) {

        case "SET_isDialogVisible": {
            return { ...my_state, isDialogVisible: my_action.new_isDialogVisible }
            break;
        }

        case "SET_carrentActiveSubdialog": {
            return { ...my_state, carrentActiveSubdialog: my_action.new_carrentActiveSubdialog }
            break;
        }

        case "SET_dialogFormRequestData": {
            return { ...my_state, dialogFormRequestData: my_action.new_dialogFormRequestData }
            break;
        }

        case "SET_carrentActiveSubdialog_2level": {
            return { ...my_state, carrentActiveSubdialog_2level: my_action.new_carrentActiveSubdialog_2level }
            break;
        }

        case "SET_carrentActiveSubdialog_5level": {
            return { ...my_state, carrentActiveSubdialog_5level: my_action.new_carrentActiveSubdialog_5level }
            break;
        }

        default: return my_state;
    }
}

// --------------------------------

const default_currentUser = {
    // bisDataRedux: new BisData_Shablon_DB(),
    currentUser: null,
    needUpdate_currentUser: false,
}

export const reduser_currentUser = (
    my_state = default_currentUser,
    my_action) => {
    switch (my_action.type) {
        case "set_currentUser": {
            // console.log("Сработал Reduser, new_currentUser =");
            // console.log(my_action.new_currentUser);
            // вносим изменения в глобальной переменной на экспорт
            GLOB_DATA.currentUser_readOnly = my_action.new_currentUser;
            return {
                ...my_state,
                currentUser: my_action.new_currentUser,
                needUpdate_currentUser: !my_state.needUpdate_currentUser, // меняем на противоположное каждый раз при срабатывании
            }
            break;
        }

        default: return my_state;
    }
}

// --------------------------------

const default_accessToken = {
    accessToken: null,
}

export const reduser_accessToken = (
    my_state = default_accessToken,
    my_action) => {
    switch (my_action.type) {

        case "setAccessToken": {
            // console.log("СРАБОТАЛ setAccessToken, new_accessToken = "+my_action.new_accessToken);
            GLOB_DATA.accessToken_readOnly = my_action.new_accessToken;
            return {
                ...my_state,
                accessToken: my_action.new_accessToken,
            }
            break;
        }

        default: return my_state;
    }
}

