
import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import BisGlobFunctions from "../bisFormsAndFunctions/bisGlobFunctions.js";
import m_SVG_ICONS from "../m_SVG_ICONS.js";

// это отдельный "выпадающий" блок с возможностью изменением верхнего значения
function DownDropLanguageChange() {
    // const [topValue, set_topValue] = useState("English");
    const [isOpenMenu, set_isOpenMenu] = useState(false);
    const current_language_DISPATCH = useSelector(mState => mState.m_Redus_languageChoosed.current_languageChoosed);

    const mStyle_langChooseRow = {
        display: "flex",
        justifyContent: "space-between",
        //width: "100%",
        marginTop: "16px",
        cursor: "pointer",
    }

    const mStyle_flagBlock = {
        height: "16px",
        width: "22px",
        marginLeft: "30px",
    }


    return (
        <div className="DownDropLanguageChange"
            style={{
                position: "relative",
                display: "flex",
                flexDirection: "column",
                // width: "fit-content",
                // border: "solid 1px grey",                
                userSelect: "none",
            }}
            onClickCapture={() => { set_isOpenMenu(!isOpenMenu) }}
            // onBlur={() => {set_isOpenMenu(false)}}
            onMouseLeave={() => { set_isOpenMenu(false) }}
        >
            {/*       Это КНОПКА выпадающего меню            */}
            <div className="button_chooseLanguage"
                style={{
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "flex-end", // нужно, иначе маркер справа смещается вверх
                    cursor: "pointer",
                    zIndex: 2, // чтоюы выпадаюзий блок не перекрывал верхнюю кнопку. И чтобы на главной кнопке правильно отображался Pointer
                }}
            >

                <div className="flagBlock"
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        alignItems: "flex-end", // нужно, иначе маркер справа смещается вверх
                        height: "16px",
                        width: "22px",
                    }}
                >


                    {(current_language_DISPATCH === "en")
                        ?
                        <img src="./noReactStaticFiles/img/flagEn.png" height={"100%"} width={"100%"} />
                        : null
                    }

                    {(current_language_DISPATCH === "de")
                        ?
                        <img src="./noReactStaticFiles/img/flagDe.png" height={"100%"} width={"100%"} />
                        : null
                    }

                    {(current_language_DISPATCH === "pl")
                        ?
                        <img src="./noReactStaticFiles/img/flagPl.png" height={"100%"} width={"100%"} />
                        : null
                    }

                    {(current_language_DISPATCH === "ua")
                        ?
                        <img src="./noReactStaticFiles/img/flagUa.png" height={"100%"} width={"100%"} />
                        : null
                    }

                    {(current_language_DISPATCH === "ru")
                        ?
                        <img src="./noReactStaticFiles/img/flagRu.png" height={"100%"} width={"100%"} title="Яхык сайта" />
                        : null
                    }

                </div>

                <div className="svg__btnSymbol"
                    style={{
                        height: "1em",
                        marginLeft: "2px",
                    }}
                >
                    <m_SVG_ICONS.vectorDown className="mSvgFile" />
                </div>

            </div>

            {/*       Это ФОН выпадающего меню            */}
            <div className="downSlideMenuContenner_posAbs"
                style={{
                    position: "absolute",
                    display: isOpenMenu ? "flex" : "none",
                    flexDirection: "column",
                    top: "10px",
                    right: 0,
                    // padding: "5px", // это влияет на невидимое поле вокруг блока, после покидания которого мышбю менб исчезает
                    paddingTop: "22px", // необходимо именно paddingTop, иначе произойдет потеря фокуса при попаданиее мышью в интервал
                                        
                    zIndex: 1, // чтоюы выпадаюзий блок не перекрывал верхнюю кнопку. И чтобы на главной кнопке правильно отображался Pointer
                    // border: "solid 1px grey", 
                }}
            >

                {/*       Это КОНТЕНТ выпадающего меню            */}
                <div className="downSlideMenuGroupBlock"
                    style={{
                        // backgroundColor: "lightcyan",
                        display: "flex",
                        flexDirection: "column",
                        backgroundColor: "rgba(50, 50, 50, 0.9)",
                        padding: "20px",
                    }}
                >

                    <div className="menuKontentBlock"
                        style={mStyle_langChooseRow}
                        onClickCapture={() => {
                            BisGlobFunctions.setlanguageChoosed("en");
                        }}
                    >
                        <div> English </div>

                        <div style={mStyle_flagBlock}>
                            <img src="./noReactStaticFiles/img/flagEn.png" height={"100%"} width={"100%"} />
                        </div>
                    </div>

                    <div className="menuKontentBlock"
                        style={mStyle_langChooseRow}
                        onClickCapture={() => {
                            BisGlobFunctions.setlanguageChoosed("de");
                        }}
                    >
                        <div> Deutsch </div>

                        <div style={mStyle_flagBlock}>
                            <img src="./noReactStaticFiles/img/flagDe.png" height={"100%"} width={"100%"} />
                        </div>
                    </div>

                    <div className="menuKontentBlock"
                        style={mStyle_langChooseRow}
                        onClickCapture={() => {
                            BisGlobFunctions.setlanguageChoosed("pl");
                        }}
                    >
                        <div> Poland </div>

                        <div style={mStyle_flagBlock}>
                            <img src="./noReactStaticFiles/img/flagPl.png" height={"100%"} width={"100%"} />
                        </div>
                    </div>

                    <div className="menuKontentBlock"
                        style={mStyle_langChooseRow}
                        onClickCapture={() => {
                            BisGlobFunctions.setlanguageChoosed("ua");
                        }}
                    >
                        <div> Ukraine </div>

                        <div style={mStyle_flagBlock}>
                            <img src="./noReactStaticFiles/img/flagUa.png" height={"100%"} width={"100%"} />
                        </div>
                    </div>

                    <div className="menuKontentBlock"
                        style={mStyle_langChooseRow}
                        onClickCapture={() => {
                            BisGlobFunctions.setlanguageChoosed("ru");
                        }}
                    >
                        <div> ru </div>

                        <div style={mStyle_flagBlock}>
                            <img src="./noReactStaticFiles/img/flagRu.png" height={"100%"} width={"100%"} />
                        </div>
                    </div>

                </div>
            </div>
        </div >
    )

}


// --------------------------------

export default DownDropLanguageChange;