const default_ActiveProjectAndSubproject_Bis = {
    currentActiveProject_INDEX: -1,
    currentActiveProject_ID: null,
    currentActiveSubproject_INDEX: -1,
    currentActiveSubproject_ID: null,
}

const reduser_ActiveProjectAndSubproject_Bis = (
    my_state = default_ActiveProjectAndSubproject_Bis,
    my_action) => {
    // console.log("Из диспатч - new_currentActivePrivateList= " + my_action.new_currentActivePrivateList);
    // console.log("Из диспатч - new_currentActivePrivateItem= " + my_action.new_currentActivePrivateItem);
    switch (my_action.type) {
        case "SET_currentActiveProject_INDEX": {
            // вырашение ниже работает и без троеточия, а также без выражения "...state," - в случае, когда состояние state представлено отдельной переменной, а не структурой данных
            // в данном случае три точки перед "state"  (...my_state) - необходимы потому, что стейт представляет структуру данных, и нам нежно не перезаписать всю структуру целиком, а только заменить в этой структуре отделльную переменную
            GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly = my_action.new_currentActiveProject_INDEX;
            return {
                ...my_state,
                currentActiveProject_INDEX: my_action.new_currentActiveProject_INDEX,
            }
            break;
        }


        // НЕ ИСПОЛЬЗУЕТСЯ ПО ФАКТУ
        case "SET_currentActiveProject_ID": {
            GLOB_DATA.activeBisProject_ID_FromRedux_readOnly = my_action.new_currentActiveProject_ID;
            return {
                ...my_state,
                currentActiveProject_ID: my_action.new_currentActiveProject_ID,
            }
            break;
        }


        case "SET_currentActiveSubproject_INDEX": {
            // вырашение ниже работает и без троеточия, а также без выражения "...state," - в случае, когда состояние state представлено отдельной переменной, а не структурой данных
            // в данном случае три точки перед "state"  (...my_state) - необходимы потому, что стейт представляет структуру данных, и нам нежно не перезаписать всю структуру целиком, а только заменить в этой структуре отделльную переменную
            GLOB_DATA.activeBis_subProject_INDEX_FromRedux_readOnly = my_action.new_currentActiveSubproject_INDEX;
            return { ...my_state, currentActiveSubproject_INDEX: my_action.new_currentActiveSubproject_INDEX }

            break;
        }

        default: return my_state;
    }
}

