const default_bisData = {
    // bisDataRedux: new BisData_Shablon_DB(),
    bisDataRedux: null,
    needUpdate_BisData: false,
}

const reduser_bisData = (
    my_state = default_bisData,
    my_action) => {
    switch (my_action.type) {
        case "UPDATE_BisData": {
            // console.log("Обновление  reduser_bisData");
            // console.log(my_action.new_bisData);
            // GLOB_DATA.bisData_VECTOR = my_action.new_bisData;
            return {
                ...my_state,
                bisDataRedux: my_action.new_bisData,
                needUpdate_BisData: !my_state.needUpdate_BisData, // меняем на противоположное каждый раз при срабатывании
            }
            break;
        }


        case "NEED_UPDATE_BisData": {
            // console.log("Обновление  NEED_UPDATE_BisData");
            return {
                ...my_state,
                needUpdate_BisData: !my_state.needUpdate_BisData, // меняем на противоположное каждый раз при срабатывании
            }
            break;
        }


        default: return my_state;
    }
}

