const default_mediaAdaptionData = {
    monitorWidth: null,
    monitorHeight: null,
    clientWidth: null,
    clientHeight: null,
    windowOrientation: "gorizontal",
    mobilAdaptationName: null
}

const reduser_mediaAdaptionData = (
    my_state = default_mediaAdaptionData,
    my_action) => {
    switch (my_action.type) {
        case "SET_mediaAdaptionData": {
            GLOB_DATA.mediaAdaptionData_VECTOR = my_action.new_mediaAdaptionData;
            return {
                my_state,
                // в отличие от других редюсеров - в "my_state" сразу разворачиваем полученный объект
                ...my_action.new_mediaAdaptionData,
            }
            break;
        }

        default: return my_state;
    }
}

