class Default_corpIndication_shablon {
    newMessages = false;
    newSettings = false;
    deadline = 0;
    subProjects_newMessages = false;
    subProjects_newSettings = false;
    subProject_deadline = 0;
}


const default_corpIndication = {
    corpIndication: new Default_corpIndication_shablon(),
    needUpdate_corpIndication: false,
}

const reduser_corpIndication = (
    my_state = default_corpIndication,
    my_action) => {
    switch (my_action.type) {
        case "change_corpIndication": {
            // console.log("РЕДАКС - change_corpIndication, deadline= ");
            GLOB_DATA.corpGlob_Indication_VECTOR = my_action.new_corpIndication;
            return {
                ...my_state,
                corpIndication: my_action.new_corpIndication,
                needUpdate_corpIndication: !my_state.needUpdate_corpIndication, // меняем на противоположное каждый раз при срабатывании
            }
            break;
        }

        default: return my_state;
    }
}


