    //----------------------------

    find_OR_AddAndFind___chatIndex_inChat_BD(project_ID) {
        let finedIndex = -1;
        finedIndex = this.find_chatIndex_inChat_BD(project_ID);
        if (!(finedIndex != null && finedIndex >= 0)) {
            GLOB_DATA.chatList_VECTOR.push(
                {
                    project_ID: project_ID,
                    messages: []
                }
            )
            finedIndex = this.find_chatIndex_inChat_BD(project_ID);
        }
        //  console.log("findIndex= "+findIndex);
        return finedIndex;
    }

    //----------------------------

    find_chatIndex_inChat_BD(project_ID) {
        let findedIndex = -1;
        if (GLOB_DATA.chatList_VECTOR) findedIndex = GLOB_DATA.chatList_VECTOR.findIndex(item => item.project_ID == project_ID);
        return findedIndex;
    }

    //----------------------------
    get_object_Individual_WasReadEvents_forCurrent_Project(project_INDEX) {
        let returnData = null;
        // console.log("project_INDEX= "+project_INDEX);
        // console.log("GLOB_DATA.bisData_VECTOR= ");
        // console.log(GLOB_DATA.bisData_VECTOR);

        if (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents) {
            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents;
        }
        return returnData;
    }
    //----------------------------
    // данная функция возвращает объект WasReadEvents для заданного субпроекта
    get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX, subProject_ID) {
        let returnData = null;
        if (
            (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents)
            && (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS)
            && (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS.length > 0)
        ) {
            // находим индекс объекта
            let findIndex = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS.findIndex(item => item.subProject_ID === subProject_ID);

            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS[findIndex];
        }
        // console.log("=== returnData= ");
        // console.log(returnData);
        return returnData;
    }
    //----------------------------
    get___OR_ADD_AND_GET___object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX, subProject_ID) {
        // console.log("ЗАПУСК get___OR_ADD_AND_GET___object_Individual_WasReadEvents_forCurrent_subProject");
        // console.log("ЗАПУСК project_INDEX=" + project_INDEX + " subProject_ID=" + subProject_ID);

        let object_Individual_WasReadEvents_forCurrent_subProject = this.get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX, subProject_ID);

        // если объект не существует или не заполнен - тогда создаем его в локальной БД 

        if (object_Individual_WasReadEvents_forCurrent_subProject === null
            || object_Individual_WasReadEvents_forCurrent_subProject === undefined
        ) {
            // посредством парсинга-репарсинга делаем отдельный экземпляр объекта из серверного шаблона класса
            let newObject_SubProjectEvents_inUserReestr = JSON.parse(
                JSON.stringify(GLOB_DATA.ShablonFromServer___SubProjectEvents_inUserReestr)
            );
            // устанавливаем пропущенный в "шаблоне" параметр project_ID
            newObject_SubProjectEvents_inUserReestr.subProject_ID = subProject_ID;

            console.log("Добавляем объект subProjects_individual_WAS_READ_EVENTS.push, newObject_SubProjectEvents_inUserReestr=");
            console.log(newObject_SubProjectEvents_inUserReestr);

            // добавляем в копию реестра
            GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS.push(newObject_SubProjectEvents_inUserReestr);
            // обновляем редакс - ОБНОВЛЕНИЕ РЕДАКСА В ЭТОМ МЕСТЕ ВЫЗЫВАЕТ ОШИБКУ В КОНСОЛИ, поскольку вызывается рендеринг элемента меню до завершения рендеринга всего списка
            GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);

            // перезапускаем поиск добавленного объекта


            object_Individual_WasReadEvents_forCurrent_subProject = this.get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX, subProject_ID);
        }

        return object_Individual_WasReadEvents_forCurrent_subProject;
    }

    //----------------------------
    get_object_timeUpdateEvents_forCurrent_Project(project_INDEX) {
        let returnData = null;
        if (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_Update_including_Objects) {
            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].time_Update_including_Objects;
        }
        return returnData;
    }
    //----------------------------
    get_object_timeUpdateEvents_forCurrent_subProject(project_INDEX, subProjects_INDEX) {
        let returnData = null;
        if (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProjects_INDEX].time_Update_including_Objects_SUBPROJECT) {
            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProjects_INDEX].time_Update_including_Objects_SUBPROJECT;
        }
        return returnData;
    }

    //----------------------------
    // функция установки свойств субпроекта Д
    set_subProject_settings(user_Email, project_ID, subProject_ID, subProjectSettings) {
        // удалить
        if (subProjectSettings == null || subProjectSettings == undefined) {
            console.log("===== ВНИМАНИЕ - ПЕРЕДАЕМ ПУСТЫЕ ДАННЫЕ, new_subProject_settings=");
            console.log(subProjectSettings);
        }

        let reqPsevdo = {
            endPoint: "set_subProject_settings",
            postDataToServer: {
                user_Email: user_Email,
                project_ID: project_ID,
                subProject_ID: subProject_ID,
                subProjectSettings: subProjectSettings,
                // currentTime_onClient_ofUpdate: Date.now(),
            }
        }
        psevdoServer(reqPsevdo);
    }

