    //----------------------------
    // Функции индикации в родительском проекте о событиях в субпроетах
    need_ParentIndication_forChat_subProjects(project_INDEX) {
        // console.log("=== project_INDEX= " + project_INDEX);
        if (project_INDEX != null && project_INDEX >= 0
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX]
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length > 0) {
            for (let index = 0; index < GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length; index++) {
                if (this.need_Indication_newMessage_chat_current_subProject(project_INDEX, index) === true) {
                    return true;
                }
            }
        }
        // по умолчанию false
        return false;
    }
    //----------------------------

    need_ParentIndication_newSettings_subProjects(project_INDEX) {
        // console.log("=== project_INDEX= " + project_INDEX);
        if (project_INDEX != null && project_INDEX >= 0
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX]
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length > 0) {
            for (let index = 0; index < GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length; index++) {
                if (this.need_Indication_newSettings_current_subProject(project_INDEX, index) === true) {
                    return true;
                }
            }
        }
        return false;
    }
