    need_ParentIndication_deadline_subProjects(project_INDEX) {
        // console.log("=== project_INDEX= " + project_INDEX);
        let returnValue = 0;
        if (project_INDEX != null && project_INDEX >= 0
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX]
            && GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length > 0) {
            for (let index = 0; index < GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects.length; index++) {
                let newValue = this.needIndication_Deadline_subProject(project_INDEX, index);
                // в след строке значение можем изменять только на большее значение
                if (newValue > returnValue) {
                    returnValue = newValue;
                }
            }
        }
        // console.log("=== ВОЗВРАЩАЕМ " + returnValue);
        return returnValue;
    }
