    needIndication_PersonalResponsible_subProject(project_INDEX, subProject_INDEX) {
        // console.log("Запуск needIndication_PersonalResponsible_subProject");
        // console.log("project_INDEX=" + project_INDEX + " subProject_INDEX=" + subProject_INDEX);

        // если отсутствуют испольнители в списке, возвращаем 0
        if (!(GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.teamList_ofResponsible_subProject)
            || GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.teamList_ofResponsible_subProject.length == 0) return 0;

        let finnedIndex = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.teamList_ofResponsible_subProject.findIndex(item => item.user_Email == GLOB_DATA.currentUser_readOnly);

        if (finnedIndex != null && finnedIndex >= 0) return 2; // текущий юзер в числе ответственных за задание
        else return 1; // текущий юзер не ответственный за задание, но есть назначенные исполнители
    }
    //----------------------------

    needIndication_Deadline_subProject(project_INDEX, subProject_INDEX) {
        // варианты returnData: 
        // 0-дедлайн не установлен, 
        // 1-дедлайн установлен, но время предупреждения не наступило, 
        // 2-наступило время напоминания о дедлайне, 
        // 3-дедлайн просрочен

        // console.log("====== needIndication_Deadline_subProject");
        // console.log("====== project_INDEX= " + project_INDEX);
        // console.log("====== subProject_INDEX= " + subProject_INDEX);

        // если отсутствуют индексы - ретерним 0
        if (
            !(project_INDEX != null && project_INDEX >= 0)
            || !(subProject_INDEX != null && subProject_INDEX >= 0)
        ) {
            return 0;
        }

        // если день дедлайна не установлен, то прерываем функцию, возвращаем 0
        if (
            !GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.deadline.deadline_Date
            || GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.deadline.deadline_Date == "----:--:--"
        ) {
            return 0;
        }

        // console.log("УСТАНАВЛИВАЕМ ИНДИКАЦИЮ ДЕДЛАЙН");

        // получаем день деадлайна в миллисекундах с учетом одних дней
        let days_Deadline = 0;
        try {
            days_Deadline = new Date(GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.deadline.deadline_Date).toISOString();
            // console.log("ДАТА до преобразования:");
            // console.log(days_Deadline);
            // далее преобразовываем дни в UTC
            days_Deadline = Date.parse(days_Deadline);
            // console.log("days_Deadline= " + days_Deadline);
        } catch (error) {
            // console.log(error);
            // console.log("ОШИБКА - days_Deadline= " + days_Deadline);
        }

        // получаем количество указанных часов дедлайна в миллисекундах
        // разбиваем строку времени на часы и минуты
        let hours_Deadline = 0;
        var split = (GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.deadline.deadline_Time).split(":");
        var hours_msec = split[0] * 60 * 60 * 1000;
        var minutes_msec = split[1] * 60 * 1000;
        var time_msec = hours_msec + minutes_msec;
        // установленное время time_msec корректно опеределилось, то используем его в расчетах
        if (time_msec) hours_Deadline = time_msec;
        // console.log("hours_Deadline= " + hours_Deadline);

        let time_Deadline_UTC = days_Deadline + hours_Deadline;
        // console.log("time_Deadline_UTC = " + time_Deadline_UTC);
        // console.log("При конвертации в станд вид: "  + new Date(time_Deadline_UTC).toLocaleDateString() + " - " + new Date(time_Deadline_UTC).toLocaleTimeString());

        let time_deadline_signalTime = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].subProjects[subProject_INDEX].subProjectSettings.deadline.deadline_signalTime_preRemind_days * 24 * 60 * 60 * 1000;
        // console.log("== time_deadline_signalTime = " + time_deadline_signalTime);

        let delaTimeToDeadline = time_Deadline_UTC - Date.now();
        // console.log("== delaTimeToDeadline = " + delaTimeToDeadline);

        // если время дедлайна наступило - возвращаем 3
        if (delaTimeToDeadline < 0) return 3;

        // если срок оповещения больше оставшегося времени, выводим предупреждение, возвращаем 2
        if (delaTimeToDeadline < time_deadline_signalTime) return 2;

        // по умолчанию ретерним оповещение, что дедлайн установлен, возвращаем 1
        return 1;
    }

