        // console.log("При конвертации в станд вид: "  + new Date(time_Deadline_UTC).toLocaleDateString() + " - " + new Date(time_Deadline_UTC).toLocaleTimeString());

        let time_deadline_signalTime = GLOB_DATA.bisData_VECTOR.projects[project_INDEX].projectSettings.deadline.deadline_signalTime_preRemind_days * 24 * 60 * 60 * 1000;
        // console.log("== time_deadline_signalTime = " + time_deadline_signalTime);

