    //----------------------------

    get_fullCorpList() {
        // создаем КОМБИНИРОВАННЫЙ  массив корп аккаунтов, включающий в название и ID аккаунта, и ID-владельца
        let combiCorpNamesAccounts = [];
        if (GLOB_DATA.bisData_VECTOR) {
            // используем разделитель _mySplit_ , чтобы потом разделить и извлечь ссумированные строковые значения           
            combiCorpNamesAccounts = GLOB_DATA.bisData_VECTOR.projects.map(
                (item, index) => {
                    // console.log("================================");
                    // console.log("item.parentCorpAccount_ID= " + item.parentCorpAccount_ID);
                    // console.log("GLOB_DATA.currentUser_readOnly= " + GLOB_DATA.currentUser_readOnly);

                    return ({
                        combi_ID: item.defaultAdminForThisProject + "_mySplit_" + item.parentCorpAccount_ID,
                        corpAccount_ID: item.parentCorpAccount_ID,
                        corpAccount_Name: item.parentCorpAccount_Name,
                        // позже удалить - заменить
                        defaultAdminForThisProject: item.defaultAdminForThisProject,
                        defaultAdmin_ID__forThisCorpAccount: item.defaultAdminForThisProject, // сюда указывать ID активного юзера
                        defaultAdmin_EMAIL__forThisCorpAccount: item.defaultAdminForThisProject, // сюда указывать EMAIL активного юзера
                        // Добавляем поле свой/чужой проект
                        isOwnCorpAccount: (item.defaultAdminForThisProject == GLOB_DATA.currentUser_readOnly ? true : false)
                    })
                });

            // далее из общего списка создаем уникальный список
            // создаем объект
            let unical_combiCorpNamesAccounts = {};

            combiCorpNamesAccounts.forEach(
                item => {
                    // добавляем/заменяем свойство с динамически созданным ключом:
                    unical_combiCorpNamesAccounts[item.combi_ID] = item;
                    // и тут же удаляем уже ненужное ("отработавшее") свойство combi_ID из объекта
                    delete item.combi_ID;
                }
            )

            // console.log("unical_combiCorpNamesAccounts после создания =");
            // console.log(unical_combiCorpNamesAccounts);

            // Из объекта извлекаем значения  в массив. С пом метода Object.values() - получаем массив значений из перечисляемых свойств объекта
            combiCorpNamesAccounts = Object.values(unical_combiCorpNamesAccounts);


            // console.log("combiCorpNamesAccounts=");
            // console.log(combiCorpNamesAccounts);

            // далее разделяем  аккаунты свои-сторонние
            let ownCorpNamesAccounts = combiCorpNamesAccounts.filter(item => item.isOwnCorpAccount == true);
            let otherCorpNamesAccounts = combiCorpNamesAccounts.filter(item => item.isOwnCorpAccount == false);

            // console.log("ownCorpNamesAccounts перед добавлением из своего списка:");
            // console.log(JSON.parse(JSON.stringify(ownCorpNamesAccounts)));

            // console.log("corpAccount_VECTOR=");
            // console.log(GLOB_DATA.corpAccount_VECTOR);


            // далее  ДЛЯ СВОИХ КОРПАККАУНТОВ - добавляем в корпЛист аккаунты, записанные ранее пользователем вручную.
            if (GLOB_DATA.bisData_VECTOR.corpAccounts
                && GLOB_DATA.bisData_VECTOR.corpAccounts.ownCorpAccounts
                && GLOB_DATA.bisData_VECTOR.corpAccounts.ownCorpAccounts.length > 0) {
                GLOB_DATA.bisData_VECTOR.corpAccounts.ownCorpAccounts.forEach(
                    (item, index) => {
                        let finedIndex = ownCorpNamesAccounts.findIndex(item_2 => item_2.corpAccount_ID === item.corpAccount_ID);
                        // если индекс не найден - значит соотв запись отсутствует, и нужно ее добавить 
                        if (!(finedIndex != null && finedIndex >= 0)) {

                            // console.log("========================");
                            // console.log("Добавляем корп акк в список, item=" + item.corpAccount_ID);

                            // запись добавляем в начало массива
                            ownCorpNamesAccounts.unshift(
                                {
                                    corpAccount_ID: item.corpAccount_ID,
                                    corpAccount_Name: item.corpAccount_Name,
                                    // позже удалить - заменить
                                    defaultAdminForThisProject: GLOB_DATA.currentUser_readOnly,
                                    defaultAdmin_ID__forThisCorpAccount: GLOB_DATA.currentUser_readOnly, // сюда указывать ID активного юзера
                                    defaultAdmin_EMAIL__forThisCorpAccount: GLOB_DATA.currentUser_readOnly, // сюда указывать EMAIL активного юзера
                                    isOwnCorpAccount: true,
                                }
                            );
                        }
                    }
                )
            }



            // console.log("ownCorpNamesAccounts ПОСЛЕ добавления из своего списка:");
            // console.log(ownCorpNamesAccounts);
            // alert("qqq");


            // далее проверяем, есть ли в списке аккаунт basicCorpAccount, и при необходимости добавляем его
            /* 
            let finedIndex = ownCorpNamesAccounts.findIndex(item => item.corpAccount_ID === "basicCorpAccount");
            // если индекс не найден - значит соотв запись отсутствует, и нужно ее добавить 
            if (finedIndex < 0) {
                // запись добавляем в начало массива
                ownCorpNamesAccounts.unshift(
                    {
                        corpAccount_ID: "basicCorpAccount",
                        corpAccount_Name: "basicCorpAccount",
                        // позже удалить - заменить
                        defaultAdminForThisProject: GLOB_DATA.currentUser_readOnly,
                        defaultAdmin_ID__forThisCorpAccount: GLOB_DATA.currentUser_readOnly, // сюда указывать ID активного юзера
                        defaultAdmin_EMAIL__forThisCorpAccount: GLOB_DATA.currentUser_readOnly, // сюда указывать EMAIL активного юзера
                        isOwnCorpAccount: true,
                    }
                );
            }
            */


            // далее склеиваем свои и прочие корпАккаунты
            combiCorpNamesAccounts = ownCorpNamesAccounts.concat(otherCorpNamesAccounts);

            // console.log("combiCorpNamesAccounts=");
            // console.log(combiCorpNamesAccounts);
        }

        return combiCorpNamesAccounts;
    }

