    //----------------------------
    // функция проверки наличия ответа сервера 401 и его обработки
    // при этом предотвратить многократный запуск этой фун
    async control_401_res(res, req_data) {
        // req_data нам нужно тут, чтобы повторить запрос после выполнения mRefreshToken и получения нового accessToken

        // console.log("ЗАПУСК control_401_res, res.status=" + res.status);
        // если ответ не содержит код 401 - тогда ничего не делаем, и передаем далее в обработку res
        if (res.status == 401) {
            // ВАЖНО - тут реализовать функцию получения новго accessToken с пом.  mRefreshToken ====================

            console.log("АНУЛИРУЕМ ТОКЕН ДОСТУПА accessToken");
            // если получить новый accessToken не удалось 
            // сначала  обнуляем accessToken
            this.setAccessToken(null);
            // потом обнуляем текущего юзера
            this.setCarrentUser(null);

            return false; // возвращаем пустой объект, чтобы не провоцировать ошибку в в прерванной вызывающей функции при попытке преобразования json()
        }

        // по умолчанию возвращаем куы для дальнейшей обработки
        return res;
    }

