    //----------------------------

    setAccessToken(accessToken) {
        // важно! Тут сначала меняем в Глоб переменной, и только потом передаем в Редакс. Т.к. Редакс отрабатывает только после выхода из вызывающей его функции, а свежий accessToken нам может понадобиться внутри вызывающей функции до того, как успеет отработать Редакс.
        GLOB_DATA.accessToken_readOnly = accessToken;
        GLOB_DATA.update_accessToken_Redux(accessToken);
        // сохраняем в ЛокалСторадж
        GLOB_DATA.locStorData.accessToken = accessToken;
        mLocalStorage.saveLocalStorageBis();
    }

