    //----------------------------

    // Эта функция создает один раз в localStorage постоянный идентификационный номер процесса, а затем возвращает его номер при входе/выхое пользователей для различения токенов доступа для различных устройств пользователя
    get_mGadgetProcess_ID_localStorage() {

        // localStorage.removeItem('mGadgetProcess_ID');        

        // пробуем определить, существует ранее созданный номер
        let data = localStorage.getItem('mGadgetProcess_ID');
        if (data) {
            // console.log("mGadgetProcess_ID обнаружен: " + data);
            // возвращаем существующий номер процесса
            return (JSON.parse(data));
        }
        else {
            // если ранее созданного номера не существует - создаем его
            let procID = "procID_" + Math.floor(Math.random() * 1000000000);
            console.log("mGadgetProcess_ID НЕ ОБНАРУЖЕН, создаем новый mGadgetProcess_ID: " + procID);
            // сохраняем номер процесса в localStorage
            localStorage.setItem('mGadgetProcess_ID', JSON.stringify(procID));
            // возвращаем созданные номер процесса
            return procID;
        }
    }

