    // Эта функция может использовать входящий аргумент "value", а может работать без его наличия
    changeSoundOn(value) {
        let newSoundOnStatus = null;
        // если мы передали конкретное значение "value" в функцию, то используем это значение. -Это происходит при загрузке из ЛокалСторадж
        // ВАЖНО - тут конкретно проверяем на наличие "true" и "false"
        if (value == true || value == false) {
            newSoundOnStatus = value;
        }
        // Иначе - если значение "value" не передавали в функцию - меняем значение soundOnStatus на противоположное
        else {
            newSoundOnStatus = !GLOB_DATA.soundOnStatus_readOnly;
        }
        // сохраняем в Редакс
        GLOB_DATA.set_soundOnStatus_Redux(newSoundOnStatus);
        // сохраняем в localStorage
        GLOB_DATA.locStorData.soundOnStatus = newSoundOnStatus;
        mLocalStorage.saveLocalStorageBis();

        // если звук включен - тогда воспроизводим его
        if (newSoundOnStatus == true) {
            this.sound_01();
            this.sound_02();
        }
    }

