    async deleteOneProject_fromLocalData(project_ID) {
        // console.log("ЗАПУСК deleteOneProject_fromLocalData, project_ID=" + project_ID);
        // если текущий проект тот, который удаляем
        if (GLOB_DATA.bisData_VECTOR.projects[GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly].project_ID == project_ID) {
            // закрываем диалоговые окна
            GLOB_DATA.set_isDialogVisible_Redux(false);
            GLOB_DATA.set_carrentActiveSubdialog_Redux(null);
            // обнуляем текущий проект в редаксе
            GLOB_DATA.update_currentActiveProject_INDEX_Redux(-1);
            // обнуляем также активный субпроект
            GLOB_DATA.update_currentActiveSubproject_INDEX_Redux(-1);
        }
        // находим индекс удаляемого проекта в локальной БД 
        let findIndex = this.find_Project_index_in_BD(project_ID);
        if (findIndex != null && findIndex >= 0) {
            GLOB_DATA.bisData_VECTOR.projects.splice(findIndex, 1);
        }
        // обновляем диспатч
        GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);

        // console.log("activeBisProject_INDEX_FromRedux_readOnly из deleteOneProject_fromLocalData= " + GLOB_DATA.activeBisProject_INDEX_FromRedux_readOnly);
    }

