    getUserRoleInProject(user_Email, project_ID) {
        let userRole = null;
        // находим проект
        let finedProjectIndex = this.find_Project_index_in_BD(project_ID);
        if (finedProjectIndex != null && finedProjectIndex >= 0) {
            // если для данного проекта defaultAdminForThisProject является текущий пользователь - тогда возвращаем "role_Admin", независимо от того, как подписан этот реальный админ в списке юзеров данного проекта (в рез пред ошибок на ранних стадиях разработки структуры данных)
            if (GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].defaultAdminForThisProject == user_Email) {
                userRole = "role_Admin";
            }
            else {
                // находим юзера в списке участников проекта
                let userIndexInProject = GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].teamList.findIndex(item => item.user_Email == user_Email);
                if (userIndexInProject != null && userIndexInProject >= 0) {
                    userRole = GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].teamList[userIndexInProject].userRole;
                }
            }
        }
        // console.log("userRole=" + userRole)
        return userRole;
    }

