    // эта функци проверяет, является ли текущий пользователь владельцем текущего корп аккаунта. Используется для допуска к добавлению нов проектов
    check_isUserOwnerForCurrentCorpAccount() {
        let returnData = false;
        if (GLOB_DATA.corpAccount_VECTOR
            && GLOB_DATA.corpAccount_VECTOR.defaultAdmin_ID__forThisCorpAccount
            && GLOB_DATA.currentUser_readOnly
            && GLOB_DATA.corpAccount_VECTOR.defaultAdmin_ID__forThisCorpAccount == GLOB_DATA.currentUser_readOnly) {
            returnData = true;
        }
        else {
            returnData = false
        }
        // console.log("check_isUserOwnerForCurrentCorpAccount= " + returnData);
        return returnData;
    }

    //----------------------------

    // эта функци проверяет, является ли текущий пользователь Админом текущего корп аккаунта. Используется для допуска к удалению проектов
    check_isUserAdminForCurrentProject(project_ID) {
        // console.log("ЗАПУСК check_isUserAdminForCurrentProject, project_ID= "+project_ID);


        let returnData = false;
        let finedProjectIndex = this.find_Project_index_in_BD(project_ID);
        if (
            (finedProjectIndex != null && finedProjectIndex >= 0)
            &&
            (GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].defaultAdminForThisProject == GLOB_DATA.currentUser_readOnly)
        ) {
            returnData = true;
        }
        // console.log("check_isUserAdminForCurrentProject= " + returnData);
        return returnData;
    }

    //----------------------------

    // эта функци проверяет, является ли текущий пользователь как минимум Модератором, либо же Админом текущего корп аккаунта. Используется для допуска к управления проектом
    check_isUserModeratorAsMinimum_forCurrentProject(project_ID) {
        let returnData = false;
        // если фвляется Админом - сразу присваиваем положительны ответ из функции 
        if (this.check_isUserAdminForCurrentProject(project_ID) == true) {
            returnData = true;
        }
        else {
            // иначе проверяем, гаходится ли пользователь в числе Модераторов данного проекта
            // находим индекс проекта]
            let finedProjectIndex = this.find_Project_index_in_BD(project_ID);
            if (finedProjectIndex != null && finedProjectIndex >= 0) {
                // находим индекс пользователя в составе команды
                let userIndexInTeam = GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].teamList.findIndex(item => item.user_Email == GLOB_DATA.currentUser_readOnly);
                if (userIndexInTeam != null && userIndexInTeam >= 0) {
                    // определяем роль пользователя
                    let userRole = GLOB_DATA.bisData_VECTOR.projects[finedProjectIndex].teamList[userIndexInTeam].userRole;
                    if (userRole == "role_Admin" || userRole == "role_Moderator") {
                        returnData = true;
                    }
                }
            }
        }
        // console.log("check_isUserModeratorAsMinimum_forCurrentProject= " + returnData);
        return returnData;
    }

