import requestService_forLongPulling from "./requestService_forLongPulling.js";
import { GLOB_DATA } from "../GLOB_DATA.js";
import BisGlobFunctions from './bisGlobFunctions.js';
import mConfig from '../config.js';
import bisGlobFunctions from "./bisGlobFunctions.js";
// let controller = new AbortController(); // будем использовать для отмены fetch
// export async function mBreackFetch() { controller.abort() }; // экспортируем для управления отменой fetch из bis-menu, когда создается новый fetch-запрос

// let lastReqID = null;
// function mSetNewlastReqID(newID) { lastReqID = newID }  // эту функции вызываем при создании нового Fetch-запроса

// оформляем постоянную подписку LongPoolling:
async function fullTimeSubscriber(user_Email, current_RequestID) {

    if (!GLOB_DATA.accessToken_readOnly) {
        console.log("=================  Прерываем fullTimeSubscriber - ОТСУТСТВУЕТ  accessToken");
        return;
    }

    if (GLOB_DATA.repeatingReq_ID___mainProcess != current_RequestID) return;

    let postDataToServer = {
        user_Email: user_Email,
    }

    let mURL = mConfig.serverPort + "/subscribeFullTime";
    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer,
            })
        })
            // Тут проверяем Статус ответа сервера
            .then(res => {
                // выполняем проверку наличие ответа 401
                BisGlobFunctions.control_401_res(res, postDataToServer);
                return res;
            })
            .then((res) => {
                console.log("====================");
                console.log("Пришел ответ сервера на fullTimeSubscriber, res= ");
                console.log(res);
                console.log("====================");
                return res;
            })
            .then(res => res.json())
            .then((res) => {
                // сначала перезапускаем longPulling для избежания либо минимизации пропуска событий на сервере
                // console.log("ПЕРЕЗАПУСКАЕМ ЛОНГПУЛЛИНГ");
                fullTimeSubscriber(user_Email, current_RequestID);
                // из полученных данных извлекаем "dataFromServer" и передаем для дальнейшей обработки
                requestService_forLongPulling(res.dataFromServer, current_RequestID);
            })
            .catch((error_2) => {
                // В случае ошибки перезапускаем запрос subscribeFullTime с задержкой 500 мсек.
                setTimeout(()=>{
                    console.log("Перезапускаем запрос subscribeFullTime");
                    fullTimeSubscriber(user_Email, current_RequestID);
                }, 500)
            })



    } catch (error) {
        console.log("мое Fetch-соединение прервано " + error);
    }
}
//--------------------------------
export { fullTimeSubscriber,  /* lastReqID, mSetNewlastReqID */ }
