export function MainMenuButtonDropDown_short() {
    const [isActiveMenu, set_isActiveMenu] = useState(false);
    const shortMenuType_Redux = useSelector(mState => mState.reduser_shortMenuType.current_shortMenuType);
    const languageChoosed_Redux = useSelector(mState => mState.m_Redus_languageChoosed.current_languageChoosed);
    return (
        //   Это  укороченная кнопка содержащая только стрелку вниз, position: "relative" 
        < div className="MainMenuButtonDropDown_short menuTopButton"
            style={{
                position: "relative",
                // display: "flex",
                display: shortMenuType_Redux ? "none" : "flex",
                flexDirection: "row",
                justifyContent: "center",
                alignItems: "center",
                padding: "2px",
                borderRadius: "1px",
                outline: "none", // необх, чтобы в мобильном приложении не было вокруг рамки при активации
                border: "none",
                height: "16px",
            }}

            title="Menu"

            // Важно - тут используем onClick , а не onClickCapture, иначе событий перехватывается на родительском элементе, что приводит к закрыванию меню
            onClick={() => {
                // при повторных кликах открываем/закрываем меню
                console.log("CLICK-1 " + Date.now());
                set_isActiveMenu(!isActiveMenu);
            }}

            onBlur={() => set_isActiveMenu(false)}
            onMouseLeave={() => set_isActiveMenu(false)}
        >

            <div className="svg__btnSymbol">
                <m_SVG_ICONS.vectorDown className="mSvgFile" />
            </div>

            {/*     Это ФОН выпадающего меню, position: "absolute"      */}
            < div className="fon_downSlider_menu"
                style={{
                    display: isActiveMenu ? 'flex' : 'none',
                    position: "absolute",
                    top: "10px",
                    left: 0,
                    // padding: "5px",
                    paddingTop: "22px",
                    // background: "red",
                    // border: "solid 1px grey",
                }}
            >

                {/*     Это КОНТЕНТ выпадающего меню      */}
                < div className="colorClass__dropDown_menu"
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        overflow: "hidden",
                        maxWidth: "300px",
                        padding: "10px",
                        // background: "green", // необх, чтобы в мобильном приложении не было вокруг рамки при активации
                    }}
                    // Важно - тут используем onClick , а не onClickCapture, иначе событий перехватывается на родительском элементе, и в сочетании с командой stopPropagation(), прерывается погружение, и не срабатывают вложенные команды меню
                    onClick={(e) => {
                        // ВАЖНО - предотвращаем всплытие события, чтобы не закрывалось меню
                        e.stopPropagation();
                        console.log("CLICK-2 " + Date.now());
                    }}
                >

                    {/*    Выбор способа отображения меню   */}
                    <div className="menuContentBlock_TextOnly"
                        style={{
                            marginTop: "20px",
                            //  marginBottom: "10px",
                            marginLeft: "10px",
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Menüanzeigemethode:" : null}
                        {(languageChoosed_Redux == "pl") ? "Sposób wyświetlania menu:" : null}
                        {(languageChoosed_Redux == "ua") ? "Спосіб відображення меню:" : null}
                        {(languageChoosed_Redux == "ru") ? "Способ отображения меню:" : null}
                        {(languageChoosed_Redux == "en") ? "Menu display method:" : null}
                    </div>

                    {/*    Это кнопка "Свернуть меню"    */}
                    <div className="menuSubContent_Button"
                        style={{
                            marginLeft: "10px",
                        }}
                        onClickCapture={() => {
                            GLOB_DATA.set_shortMenuType(true);
                        }}
                    >
                        <div className="check_Block">
                            <input
                                type="checkbox"
                                checked={
                                    (shortMenuType_Redux)
                                        ? true
                                        : false
                                }

                                // делаем его недоступным для нажатия мыши, посколько оно перехватывает Клик по блоку, и не срабатывает фугкционал. Нажатие реализовано через клик по всему блоку.
                                style={{ pointerEvents: "none" }}
                                readOnly={true}
                            />
                        </div>

                        <div className="optionText"
                            style={{
                                marginLeft: "10px",
                            }}
                        >
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Kompakt" : null}
                            {(languageChoosed_Redux == "pl") ? "Kompaktowy" : null}
                            {(languageChoosed_Redux == "ua") ? "Компактний" : null}
                            {(languageChoosed_Redux == "ru") ? "Компактный" : null}
                            {(languageChoosed_Redux == "en") ? "Compact" : null}
                        </div>
                    </div>

                    {/*    Это кнопка "Развернуть меню"    */}
                    <div className="menuSubContent_Button"
                        style={{
                            marginLeft: "10px",
                        }}
                        onClickCapture={() => {
                            GLOB_DATA.set_shortMenuType(false);
                        }}
                    >
                        <div className="check_Block">
                            <input
                                type="checkbox"
                                checked={
                                    (shortMenuType_Redux)
                                        ? false
                                        : true
                                }
                                // делаем его недоступным для нажатия мыши, посколько оно перехватывает Клик по блоку, и не срабатывает фугкционал. Нажатие реализовано через клик по всему блоку.
                                style={{ pointerEvents: "none" }}
                                readOnly={true}
                            />
                        </div>

                        <div className="optionText"
                            style={{
                                marginLeft: "10px",
                            }}
                        >
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Erweitert" : null}
                            {(languageChoosed_Redux == "pl") ? "Rozszerzony" : null}
                            {(languageChoosed_Redux == "ua") ? "Розгорнутий" : null}
                            {(languageChoosed_Redux == "ru") ? "Развернутый" : null}
                            {(languageChoosed_Redux == "en") ? "Expanded" : null}
                        </div>
                    </div>
                </div >
            </div >

        </div >



    )
}

