// пока эта функция не используется
function mRefreshToken() {
    console.log("ОТПРАВЛЯЕМ ЗАПРОС mRefreshToken ");
    let postDataToServer = {
        eMail: GLOB_DATA.currentUser_readOnly,
        mGadgetProcess_ID: GLOB_DATA.mGadgetProcess_ID,
    }
    console.log("document.cookie= ");
    console.log(document.cookie);
    // включаем предохранитель от многкратного запуска mRefreshToken
    let mURL = mConfig.serverPort + "/refreshToken";
    fetch(mURL, {
        method: 'post',
        headers: {
            accesstoken: GLOB_DATA.accessToken_readOnly,
            'Content-Type': 'application/json',
            // 'Cookies': 'refreshToken'
        },
        // для включения файлов куки в запрос
        credentials: "include",

        body: JSON.stringify({
            postDataToServer: postDataToServer,
        }),
    })
        .then(res => res.json())
        .then((res) => {
            console.log("Возвращен ответ loginService --- mRefreshToken: ");
            console.log(res);
            // сохраняем обновленный accessToken
            GLOB_DATA.update_accessToken_Redux(res.accessToken);

        })
}

