                                            (languageChoosed_Redux == "de" ? "Aufgabe hinzufügen" : "") +
                                            (languageChoosed_Redux == "pl" ? "Dodaj zadanie" : "") +
                                            (languageChoosed_Redux == "ua" ? "Додати завдання" : "") +
                                            (languageChoosed_Redux == "ru" ? "Добавить задание" : "") +
                                            (languageChoosed_Redux == "en" ? "Add task" : "")
