function TeamCurrentProject(props) {
    const bisDataRedux = useSelector(mState => mState.m_Redus_bisData.bisDataRedux);
    
    const usersOnlineStatusRedux = useSelector(mState => mState.m_Redus_usersOnlineStatus.usersOnlineStatusRedux);

    const [isOnlineCurrentUser, set_isOnlineCurrentUser] = useState(false);

    let teamList = "";

    if (props.project_INDEX != null && props.project_INDEX >= 0) {
        // console.log("bisDataRedux.projects[props.project_INDEX].teamList=");
        // console.log(bisDataRedux.projects[props.project_INDEX].teamList);

        teamList = bisDataRedux.projects[props.project_INDEX].teamList.map(
            (item, index) => (
                <div className="TeamCurrentProject"
                    key={Math.random()}
                >
                    <UserAvatarBlock
                        userName={item.user_Email} // установить имя
                        user_Email={item.user_Email}
                        isOnlineCurrentUser={isOnlineCurrentUser} // установить фактический онлайн статус
                        avatarSize_PX="30px"

                    />
                </div>
            )
        )
    }
    return teamList;
}

