function TeamCurrent_subProject(props) {
    const bisDataRedux = useSelector(mState => mState.m_Redus_bisData.bisDataRedux);

    let teamList = "";

    console.log("teamList_ofResponsible_subProject=");
    console.log(bisDataRedux.projects[props.project_INDEX].subProjects[props.subProject_INDEX].teamList_ofResponsible_subProject);

    if ((props.project_INDEX != null && props.project_INDEX >= 0) && (props.subProject_INDEX != null && props.subProject_INDEX >= 0)) {
        teamList = bisDataRedux.projects[props.project_INDEX].subProjects[props.subProject_INDEX].teamList_ofResponsible_subProject.map(
            (item, index) => (
                <div className="avatarBlock_TeamCurrent_subProject"
                    key={Math.random()}
                >
                    <UserAvatarBlock
                        userName={item.user_Email} // установить имя
                        user_Email={item.user_Email}
                        isOnlineCurrentUser={false} // установить фактический онлайн статус
                        avatarSize_PX="30px"
                    />
                </div>
            )
        )
    }
    return teamList;
}

