import React from 'react';
import { useDispatch, useSelector } from "react-redux";
import './main.css';
// import { myTD_BS, saveLocalStorage, loadLocalStorage } from '../privateFormsAndFunctions/m_private_functions.js';
// import Header from "../header/header";
// import { PrivateListMenu, CurrentPrivateList } from '../privateFormsAndFunctions/m_private_forms';
import { BisMenu } from '../bisFormsAndFunctions/bis_menu.jsx';
import { BisSubContent } from '../bisFormsAndFunctions/bis_GlobContenner.jsx';
import DialogForm from './dialogForm.jsx';


function Main() {
    const my_dispatch = useDispatch();
    const selected_private_or_bis_FromRedux = String(useSelector(mState => mState.m_Redus_select_Priv_or_Bis.select_priv_bis));
    const mediaAdaptationData_mobilAdaptationName_FromRedux = useSelector(mState => mState.m_Redus_mediaAdaptionData.mobilAdaptationName);

    return (
        <main
            style={
                (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01")
                    ? { bottom: "6px" }
                    : {}
            }
        >
            {/*    <Header />    */}

            <div className="bisContent">

                <div className='bisContent_LEFT'
                    // медиаадаптация
                    style={
                        (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01") ? { width: "100%", maxWidth: "600px", marginRight: "10px"} : {}
                    }
                >
                    <BisMenu />
                </div>

                <div className="bisContent_CENTER"
                    // медиаадаптация
                    style={
                        (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01") ? { display: "none" } : {}
                    }
                >
                    < BisSubContent />
                </div>

            </div>

{/* 
            <div className={(selected_private_or_bis_FromRedux === "priv") ? "privContent" : "mDisplayNone"} >
                <div className='privContent_LEFT'>
                    <PrivateListMenu />
                </div>
                <div className='privContent_CENTER'>
                    < CurrentPrivateList />
                </div>
            </div>
 */}

            {/* по умолчанию скрыто  */}
            {/*     <DialogForm /> */}

        </main >
    )
}

//--------------------
export default Main;


