    function SubDialog_changePasswForm(props) {

        const [userEmail, set_userEmail] = useState(props.user_Email);
        const [newPassword, set_newPassword] = useState(null);
        const [isCorrectEmail, set_isCorrectEmail] = useState(false);
        const [isCorrectPassw, set_isCorrectPassw] = useState(false);

        const ref_headAvatarImage = useRef();
        const ref_inputUserEmail = useRef();
        const ref_inputNewPassword = useRef();

        function validationPass(value) {
            if (
                value
                && value.length >= 6
                && value.length <= 50
            ) {
                set_isCorrectPassw(true);
                return true;
            }
            else {
                set_isCorrectPassw(false);
                return false;
            }
        }

        function validationEmail(value) {
            // исправить - провести валидацию данных
            if (
                value
            ) {
                set_isCorrectEmail(true);
                return true;
            }
            else {
                set_isCorrectEmail(false);
                return false;
            }
        }



        return (
            <div
                className="subDialogForm_styleType01"
                className={
                    (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01")
                        ? "subDialogForm_styleType01 additional_Form_mobile_01"
                        : "subDialogForm_styleType01"
                }
            >

                <div className="SubDialog_changePasswForm flex_column">

                    <div className="SubDialog_changePasswForm_TOP flex_column">

                        <div className="flex_column">
                            <div className="texStyle_formHeader">
                                {/*    Языковой блок    */}
                                {(languageChoosed_Redux == "de") ? "KENNWORT ÄNDERN" : null}
                                {(languageChoosed_Redux == "pl") ? "ZMIEŃ HASŁO" : null}
                                {(languageChoosed_Redux == "ua") ? "ЗМІНА ПАРОЛЯ" : null}
                                {(languageChoosed_Redux == "ru") ? "ИЗМЕНЕНИЕ ПАРОЛЯ" : null}
                                {(languageChoosed_Redux == "en") ? "CHANGE PASSWORD" : null}
                            </div>

                            <br />

                            <div className="imgBlock"
                                style={{
                                    height: "40px",
                                    //width: "140px",
                                }}
                            >
                                <img
                                    ref={ref_headAvatarImage}
                                    src={mConfig.serverPort + "/avatars/av___" + currentUser_Redux + ".jpeg"}
                                    height="100%"
                                />
                            </div>

                            <div> {currentUser_Redux}  </div>
                            <br />
                        </div>

                        <div className="userInfoContenner flex_column">

                            <br />
                            <br />

                            <div className="flex_column">
                                {/*    Языковой блок    */}
                                {(languageChoosed_Redux == "de") ? "Benutzer" : null}
                                {(languageChoosed_Redux == "pl") ? "Użytkownik" : null}
                                {(languageChoosed_Redux == "ua") ? "Користувач" : null}
                                {(languageChoosed_Redux == "ru") ? "Пользователь" : null}
                                {(languageChoosed_Redux == "en") ? "User" : null}

                                (Email):
                                <input
                                    type="email"
                                    ref={ref_inputUserEmail}
                                    style={{ border: "solid 1px" }}
                                    defaultValue={props.user_Email}
                                    onChangeCapture={() => {
                                        set_userEmail(ref_inputUserEmail.current.value);
                                        validationEmail(ref_inputUserEmail.current.value);
                                    }}
                                />


                                <div>
                                    {props.user_Email}
                                </div>
                            </div>

                            <br />

                            <div className="flex_column">
                                <div>
                                    {/*    Языковой блок    */}
                                    {(languageChoosed_Redux == "de") ? "Neues Passwort:" : null}
                                    {(languageChoosed_Redux == "pl") ? "Nowe hasło:" : null}
                                    {(languageChoosed_Redux == "ua") ? "Новий пароль:" : null}
                                    {(languageChoosed_Redux == "ru") ? "Новый пароль:" : null}
                                    {(languageChoosed_Redux == "en") ? "New Password:" : null}
                                </div>
                                <input
                                    type="password"
                                    ref={ref_inputNewPassword}
                                    style={{ border: "solid 1px" }}
                                    onChangeCapture={() => {
                                        set_newPassword(ref_inputNewPassword.current.value);
                                        validationPass(ref_inputNewPassword.current.value);
                                    }}
                                />
                            </div>

                            <br />
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Sie erhalten eine E-Mail zur Aktivierung Ihres neuen Passworts. Sie müssen diese E-Mail öffnen und auf den Bestätigungslink klicken." : null}
                            {(languageChoosed_Redux == "pl") ? "Wiadomość e-mail zostanie wysłana do Ciebie, aby aktywować nowe hasło. Musisz otworzyć tę wiadomość e-mail i kliknąć link potwierdzający." : null}
                            {(languageChoosed_Redux == "ua") ? "Щоб активувати новий пароль, вам буде надіслано повідомлення на вашу пошту. Вам необхідно відкрити цей лист і натиснути посилання підтвердження." : null}
                            {(languageChoosed_Redux == "ru") ? "Для активации нового пароля вам будет отправлено сообщение на вашу почту. Вам необходимо открыть данное письмо и кликнуть ссылку подтверждения." : null}
                            {(languageChoosed_Redux == "en") ? "An email will be sent to you to activate your new password. You need to open this email and click the confirmation link." : null}

                            <br />
                            <br />

                            <button className="mBtn_01"
                                disabled={
                                    (isCorrectPassw && isCorrectEmail)
                                        ? false
                                        : true
                                }

                                onClickCapture={async () => {
                                    let req_loginService = {
                                        endPoint: "changePassword",
                                        postDataToServer: {
                                            user_Email: userEmail,
                                            newPassword: newPassword,
                                            mGadgetProcess_ID: GLOB_DATA.mGadgetProcess_ID,
                                        }
                                    }
                                    let promiseAnswerData = await loginService(req_loginService);
                                    console.log("promiseAnswerData=");
                                    console.log(promiseAnswerData);
                                }}
                            >
                                {/*    Языковой блок    */}
                                {(languageChoosed_Redux == "de") ? "Schicken" : null}
                                {(languageChoosed_Redux == "pl") ? "Wysłać" : null}
                                {(languageChoosed_Redux == "ua") ? "Відправити" : null}
                                {(languageChoosed_Redux == "ru") ? "Отправить" : null}
                                {(languageChoosed_Redux == "en") ? "Send" : null}
                            </button>

                        </div>

                        <br />
                        <br />

                    </div>

                    <div className="SubDialog_changePasswForm_BOTTOM flex_column">


                        <div className='buttonsBlock'>

                            <button className="mBtn_01"
                                onClickCapture={() => {
                                    GLOB_DATA.closeDialogForm();
                                }}
                            >
                                {/*    Языковой блок    */}
                                {(languageChoosed_Redux == "de") ? "Stornieren" : null}
                                {(languageChoosed_Redux == "pl") ? "Anulować" : null}
                                {(languageChoosed_Redux == "ua") ? "Скасувати" : null}
                                {(languageChoosed_Redux == "ru") ? "Отмена" : null}
                                {(languageChoosed_Redux == "en") ? "Cancel" : null}
                            </button>

                        </div>

                    </div>

                </div>

            </div >
        )
    }

