

import { createStore, combineReducers } from "redux";
import { reduser_select_Priv_or_Bis, reduser_colorSheme, reduser_soundOnStatus, reduser_languageChoosed, reduser_DialogForm, reduser_currentUser, reduser_accessToken, reduser_shortMenuType } from "./reduserMain.js";
import { reduser_ActiveProjectAndSubproject_Bis, reduser_bisData, reduser_chatListRedux, reduser_corpAccount, reduser_corpList, reduser_corpList_indication, reduser_corpIndication, reduser_usersOnlineStatus, reduser_mediaAdaptionData } from "./reduserBis.js";


// --------------------------------------
// все редюсеры
const all_redusers = combineReducers(
    {
        // редюсеры можно передавать просто списком, а можно структурой "ключ: значение"
        m_Redus_select_Priv_or_Bis: reduser_select_Priv_or_Bis,
        m_Redus_colorSheme: reduser_colorSheme,
        m_Redus_soundOnStatus: reduser_soundOnStatus,
        m_Redus_languageChoosed: reduser_languageChoosed,
        reduser_shortMenuType: reduser_shortMenuType,

        m_Redus_DialogForm: reduser_DialogForm,
        m_Redus_currentUser: reduser_currentUser,
        m_Redus_accessToken: reduser_accessToken,

        // corporative Data:
        m_Redus_corpAccount: reduser_corpAccount,
        m_Redus_corpList: reduser_corpList, // по факту этот редюсер не используется
        m_Redus_corpList_indication: reduser_corpList_indication,
        m_Redus_corpIndication: reduser_corpIndication,

        m_Redus_ActiveProjectAndSubproject_Bis: reduser_ActiveProjectAndSubproject_Bis,
        // m_Redus_userReestrDataRedux: reduser_userReestrDataRedux,
        m_Redus_bisData: reduser_bisData,
        m_Redus_usersOnlineStatus: reduser_usersOnlineStatus,
        m_Redus_chatListRedux: reduser_chatListRedux,

        m_Redus_mediaAdaptionData: reduser_mediaAdaptionData,

    }
)

// экспортируем все сторы для их передачи в главный файл приложения
const globStore = createStore(all_redusers);
export { globStore };








